.bg-photo {
  z-index: -9;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 35%;
  background-image: url(../src/image/main-bg.jpg);
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;

  -webkit-animation: bg-photo 1.2s cubic-bezier(0.5, 0.6, 0.6, 1);
  -moz-animation: bg-photo 1.2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation: bg-photo 1.2s cubic-bezier(0.5, 0.6, 0.6, 1);
  -webkit-animation-fill-mode: backwards;
  -moz-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
}
@media screen and (max-width: 767px) {
  .bg-photo {
    -webkit-animation: bg-photo 0.6s cubic-bezier(0.5, 0.6, 0.6, 1);
    -moz-animation: bg-photo 0.6s cubic-bezier(0.5, 0.6, 0.6, 1);
    animation: bg-photo 0.6s cubic-bezier(0.5, 0.6, 0.6, 1);
  }
}
@-webkit-keyframes bg-photo {
  0% {
    opacity: 0;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes bg-photo {
  0% {
    opacity: 0;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@media screen and (max-width: 480px) {
  .bg-photo {
    left: -8%;
  }
}

