/* ------ Hover states ------ */
.on-hover-40,
.on-hover--40 {
  transition: all 0.4s cubic-bezier(0.2, 0.6, 0.2, 1);
}
.on-hover-40:hover {
  margin-left: 40px;
  transition: all 0.4s cubic-bezier(0.2, 0.6, 0.2, 1);
  text-decoration: none !important;
}
.on-hover--40:hover {
  margin-left: -40px;
  transition: all 0.4s cubic-bezier(0.2, 0.6, 0.2, 1);
  text-decoration: none !important;
}
@media screen and (max-width: 767px) {
  .on-hover-40:hover {
    margin-left: 0px;
  }
  .on-hover--40:hover {
    margin-left: 0px;
  }
}

