  -moz-animation: fadelogo 0.6s cubic-bezier(0.2, 0.6, 0.2, 1);
  animation: fadelogo 0.6s cubic-bezier(0.2, 0.6, 0.2, 1);
  -webkit-animation-fill-mode: backwards;
  -moz-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
}
@-webkit-keyframes fadelogo {
  0% {
    -webkit-transform: translateY(10vh);
  }
  100% {
    -webkit-transform: translateY(0vh);
  }
}
@keyframes fadelogo {
  0% {
    opacity: 0;
    transform: translateY(10vh);
  }
  100% {
    opacity: 1;
    transform: translateY(0vh);
  }
}

/* ------ Reading position ------ */
.rpi-progress-bar {
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  padding-bottom: 20px;
  z-index: 1;
}
.rpi-progress-bar__position {
  box-sizing: inherit;
  height: 4px; /* control the height of the progress bar */
  width: 100%;
  background-color: #d1001d;
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transition: -webkit-transform 50ms;
  transition: -webkit-transform 50ms;
  transition: transform 50ms;
  transition: transform 50ms, -webkit-transform 50ms;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  will-change: transform;
  position: relative;
}

