    display: none;
  }
  .mobile-tap-area {
    background: transparent;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .tap-indication {
    text-align: center;
    width: 100%;
    font-family: 'Medium';
    color: #ffffff;
    position: fixed;
    bottom: 4%;
    font-size: 0.6em; /* general fallback */
    font-size: 3.6vm; /* IE9 fallback */
    font-size: 3.6vmin;
    animation: bounce 1s infinite alternate;

    -webkit-animation: bounce 0.5s cubic-bezier(0.5, 0.6, 0.6, 1) infinite
      alternate;
    -moz-animation: bounce 0.5s cubic-bezier(0.5, 0.6, 0.6, 1) infinite
      alternate;
    animation: bounce 0.5s cubic-bezier(0.5, 0.6, 0.6, 1) infinite alternate;
  }
  @keyframes bounce {
    from {
      transform: translateY(0px);
    }
    to {
      transform: translateY(-8px);
    }
  }
  @-webkit-keyframes bounce {
    from {
      transform: translateY(0px);
    }
    to {
      transform: translateY(-8px);
    }
  }
}

